﻿.quizQuestion {
  padding-top: 1em;
  font-size: 28px;
  font-weight: bolder;
  font-family: "Open Sans";
  color: "#5E5E5E";
}

.optionSection {
  padding-top: 3em;
  font-family: "Open Sans";
}

.optionSection .contain .checkmark::after {
  top: 9.3px;
  left: 9.5px;
  width: 20px;
  height: 20px;
}

.optionSection label {
  padding: 1em 0;
}

.optionSection label span.question {
  position: relative;
  display: inline-block;
  padding-left: 6%;
  bottom: 10px;
}

.optionSection .checkmark {
  height: 40px;
  width: 40px;
}

.optionSection input[type="radio"] {
  width: 50%;
}

.next-button{
  border-radius: 22px;
    padding-left: 35px;
    padding-top: 10px;
    padding-right: 35px;
    padding-bottom: 10px;
    background-color: #10A3FF;
    border: none;
    color: #fff;
    font-size: 14px;
    margin-right: 15px;
}

.previous-button{
  border-radius: 22px;
    padding-left: 35px;
    padding-top: 10px;
    padding-right: 35px;
    padding-bottom: 10px;
    background-color: transparent;
    border: 1px solid #1e1e1e;
    color: $studyblue;
    font-size: 14px;
    margin-right: 15px;
}

.next-button:focus {
  outline: none;
}
.previous-button:focus {
  outline: none;
}