﻿.sidebar {
  color: #fff;
  background-color: white;
  box-shadow: 9px 3px 12px rgba(255, 255, 255, 0.1);
  width: 250px;
  max-width: 250px;
  height: 100%;
  float: left;
  position: relative;
  z-index: 1000;
  display: block;
  -webkit-transition: margin 1s;
  transition: margin 1s;
  flex: 1;
  padding-top: 6em;
  overflow-y: scroll;
  height: 87vh;

  h5 {
    font-weight: bold;
    color: $studyblue;
  }

  h6 {
    color: black;
  }

  .dropdown-toggle::after {
    display: none;
  }

  .dropdown {
    button {
      background-color: transparent;
      border: none;
      outline: none;
      width: 100%;
      padding: 0.9em 4.5em;
      text-align: left;
      font-size: 13px;
      color: #8e8e8e;

      &:focus {
        outline: none;
        box-shadow: none;
        border: none;
      }

      img {
        padding: 0 1em;
      }
    }
  }

  .dropdown-item {
    position: relative;

    &:hover {
      background-color: $studyblue;
      //color: white !important;
    }
  }
}

#navbar-toggle {
  cursor: pointer;
}

#toggleView {
  margin-left: 44px;
}

.menu-icon {
  float: right;
}

.sidebar-nav {
  display: block;
  float: left;
  width: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
}

.sidebar-nav li a {
  padding-left: 20px;
  font-size: 16px;
  text-decoration: none;
  color: black;
  float: left;
  text-decoration: none;
  width: 100%;
  height: 70px;
  line-height: 25px;
  padding: 20px;
  vertical-align: center;
}

.sidebar-nav li a:hover {
  cursor: pointer;
  -webkit-transition: background 0.5s;
  transition: background 0.5s;
}
.logout-text{
  font-size: 11px;
  color: #AAAAAA;
  font-weight: bolder;
  margin-left: 20px;
}

.log-out {
  z-index: 2000;
  position: fixed;
  bottom: 0;
  padding-bottom: 15px;
  background-color: #fff;
}

.selected-profile{
  margin-bottom: 20px;
  img{
    width: 50%;
    border-radius: 50%;
    box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.1), 0 2px 2px 0 rgba(0, 0, 0, 0.4);
  }
}