﻿ul {
  list-style-type: none;
  padding-left: 0;
}

a {
  color: #b63b4d;
  text-decoration: none;
}

h1 {
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  text-align: center;
  margin-top: 80px;
}

h1 a {
  color: #c12c42;
  font-size: 16px;
}

.accordion {
  width: 100%;
  max-width: 360px;
  margin: 0px auto 5px;
  background: #fff;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
}

.accordion .link {
  cursor: pointer;
  display: block;
  padding: 15px 15px 15px 25px;
  font-size: 14px;
  color: #4d4d4d;
  font-weight: 700;
  position: relative;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;

  span {
    padding-left: 15px;
    padding-right: 25px;
  }
}

.accordion li:last-child .link {
  border-bottom: 0;
}

.accordion li img {
  padding-left: 1px !important;
}

.accordion li i.fa-chevron-down {
  right: 12px;
  float: right;
  top: 3px;
  font-size: 12px;
  position: relative;
  color: #10a3ff;
}

/*.accordion li.open .link {
    color: #b63b4d;
}

.accordion li.open i {
    color: #b63b4d;
}*/

.accordion li.open i.fa-chevron-down {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}

.submenu {
  font-size: 14px;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;

  .dropdown-menu {
    border-radius: 0px;
    box-shadow: none;
    border: none !important;
  }
}

.submenu li {
  position: relative;
}

.submenu {
  button {
    span {
      position: relative;

      &:hover {
        color: $studyblue;
      }
    }
  }
}

