﻿.register-wrapper {
    background-image: url("../../../img/register-background.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    min-height: 100%;
    height: 100vh;
    font-family: $sans !important;

    @media screen and (max-width: 768px) {
        height: 100% !important;
    }

    .container {
        padding-top: 13%;
        
        .package {

            h2 {
                color: $white;
                font-weight: bolder;
                font-family: $sans
            }

            .packages {
                margin: 0;

                .package-content {
                    @media screen and (min-width: 768px) {
                        padding-top: 20px;
                    }

                    &:before {
                        content: url("../../../img/check.png");
                        position: absolute;
                        bottom: 17px;

                        @media screen and (max-width:768px) {
                            bottom: 36px;
                        }
                    }

                    .wrap-text {
                        display: inline-block;
                        padding-left: 35px;
                        margin-top: 10px;

                        span.head {
                            font-weight: bold;
                            color: $white;
                            line-height: 1.8;
                        }

                        span.desc {
                            color: $white;

                            @media screen and (max-width:768px) {
                                font-size: 11px !important;
                            }
                        }
                    }
                }
            }
        }

        .register-details {
            background-color: $white;
            padding: 2em;
            width: 100%;

            .form-group {
                margin-bottom: 6px !important;
            }

            input {
                border-radius: 0px !important;
                padding: 1.8em;
                font-size: 12px;

                &:focus {
                    outline: none;
                    border: none;
                }
            }

            .select_ {
                border-radius: 0px !important;
                height: 45px !important;
                font-size: 12px;
            }

            button.register {
                background-color: $studyblue;
                border: none;
                outline: none;
                width: 100%;
                margin: 0 auto;
                padding: 0.8em 2em;
                color: white;
                margin-top: 1em;
                font-weight: bold;
                text-transform: uppercase;
            }
        }
    }
}
