﻿.video-section {
  padding: 3em;
  background-color: #fafafa;
}

.video-container {
  position: relative;
  overflow-y: scroll;
  height: 80vh;
}

.video-title h2 {
  font-weight: bolder;
  color: #5e5e5e;
}

.video-description h5 {
  line-height: 2;
  font-size: 12px;
}

.video-item h5 {
  font-weight: bolder;
  margin-top: 0px;
}

.short-desc {
  font-size: 12px;
}

.videoList-container {
  height: 80vh;
  overflow-x: hidden;
  overflow-y: scroll;
}

.videoList-container {
  .video-item {
    margin-bottom: 1.5em;

    &:hover {
      cursor: pointer;
    }
  }
}

.videoList-container > .video-item img {
  position: relative;
}
